import React, { useState } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import { useInView } from 'react-intersection-observer'
import { Play, Code, Download, X, Cpu, Zap, CheckCircle, Clock } from 'lucide-react'
import CountUp from 'react-countup'

const Demo: React.FC = () => {
  const [ref, inView] = useInView({ 
    threshold: 0.2, 
    triggerOnce: true 
  })
  const [showDemoModal, setShowDemoModal] = useState(false)
  const [demoStatus, setDemoStatus] = useState<'idle' | 'running' | 'completed'>('idle')

  const metrics = [
    { value: 7.5, label: ' ', unit: '', icon: Clock },
    { value: 16, label: ' ', unit: 'GB', icon: Cpu },
    { value: 100, label: ' ', unit: '%', icon: CheckCircle }
  ]

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.2,
        delayChildren: 0.3
      }
    }
  }

  const itemVariants = {
    hidden: { y: 50, opacity: 0 },
    visible: {
      y: 0,
      opacity: 1,
      transition: {
        type: "spring",
        stiffness: 100,
        damping: 12
      }
    }
  }

  const handleRunDemo = () => {
    setShowDemoModal(true)
    setDemoStatus('running')
    
    // Simulate demo execution
    setTimeout(() => {
      setDemoStatus('completed')
    }, 3000)
  }

  const downloadReport = () => {
    const reportContent = `
OZON VRP Solution - Performance Report
Generated: ${new Date().toLocaleDateString('ru-RU')}

=== EXECUTIVE SUMMARY ===
- Processing Time: 7.5 minutes (Target: < 3 hours) 
- Memory Usage: < 16GB (Limit: 32GB)   
- Graph Compression: 99.75% (400M  1M edges) 
- Data Loading: 4.2 minutes 

=== TECHNICAL METRICS ===
Orders Processed: 20,160
Couriers Optimized: 200
Total Constraints: Satisfied
Production Ready: Yes

=== ARCHITECTURE ===
1. Data Infrastructure Pipeline 
2. Graph Neural Network   
3. Attention Decoder 
4. Reinforcement Learning 

Report generated by OZON VRP Solution
    `
    
    const blob = new Blob([reportContent], { type: 'text/plain;charset=utf-8' })
    const url = URL.createObjectURL(blob)
    const a = document.createElement('a')
    a.href = url
    a.download = `OZON_VRP_Report_${new Date().toISOString().split('T')[0]}.txt`
    document.body.appendChild(a)
    a.click()
    document.body.removeChild(a)
    URL.revokeObjectURL(url)
  }

  return (
    <section id="demo" className="demo" ref={ref}>
      <div className="container">
        <motion.div
          variants={containerVariants}
          initial="hidden"
          animate={inView ? "visible" : "hidden"}
          className="text-center mb-16"
        >
          <motion.h2 variants={itemVariants}> </motion.h2>
          
          <motion.p 
            variants={itemVariants}
            className="demo-description"
          >
                     
              OZON    .
          </motion.p>

          {/* Action Buttons */}
          <motion.div
            variants={itemVariants}
            className="demo-buttons"
          >
            <motion.button
              onClick={handleRunDemo}
              className="btn-demo primary cursor-target"
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
            >
               
            </motion.button>

            <motion.button
              onClick={() => window.open('https://github.com/your-repo/ozon-vrp', '_blank')}
              className="btn-demo secondary cursor-target"
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
            >
               
            </motion.button>

            <motion.button
              onClick={downloadReport}
              className="btn-demo secondary cursor-target"
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
            >
               
            </motion.button>
          </motion.div>
        </motion.div>

        {/* Metrics */}
        <motion.div
          variants={containerVariants}
          initial="hidden"
          animate={inView ? "visible" : "hidden"}
          className="demo-metrics"
        >
          {metrics.map((metric, index) => (
            <motion.div
              key={index}
              variants={itemVariants}
              className="metric"
              whileHover={{ scale: 1.05 }}
            >
              <motion.div
                className="metric-value"
                initial={{ scale: 0 }}
                animate={inView ? { scale: 1 } : { scale: 0 }}
                transition={{ delay: index * 0.2 + 0.5, type: "spring", stiffness: 200 }}
              >
                {inView && (
                  <CountUp
                    end={metric.value}
                    duration={2}
                    delay={index * 0.2 + 0.5}
                    decimals={metric.value % 1 !== 0 ? 1 : 0}
                  />
                )}
                <span>{metric.unit}</span>
              </motion.div>

              <motion.p
                className="metric-label"
                initial={{ opacity: 0, y: 20 }}
                animate={inView ? { opacity: 1, y: 0 } : { opacity: 0, y: 20 }}
                transition={{ delay: index * 0.2 + 0.7 }}
              >
                {metric.label}
              </motion.p>
            </motion.div>
          ))}
        </motion.div>
      </div>

      {/* Demo Modal */}
      <AnimatePresence>
        {showDemoModal && (
          <motion.div
            className="fixed inset-0 bg-black/80 backdrop-blur-sm z-50 flex items-center justify-center p-4"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            onClick={() => setShowDemoModal(false)}
          >
            <motion.div
              className="bg-white rounded-3xl max-w-2xl w-full max-h-[80vh] overflow-y-auto"
              initial={{ scale: 0.8, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              exit={{ scale: 0.8, opacity: 0 }}
              onClick={(e) => e.stopPropagation()}
            >
              {/* Modal Header */}
              <div className="flex items-center justify-between p-6 border-b border-gray-200">
                <h3 className="text-2xl font-bold text-gray-900 flex items-center space-x-2">
                  <Zap className="w-8 h-8 text-primary-500" />
                  <span>OZON VRP Demo</span>
                </h3>
                <button
                  onClick={() => setShowDemoModal(false)}
                  className="p-2 hover:bg-gray-100 rounded-full transition-colors"
                >
                  <X className="w-6 h-6 text-gray-500" />
                </button>
              </div>

              {/* Modal Body */}
              <div className="p-6">
                {demoStatus === 'running' && (
                  <motion.div
                    className="text-center"
                    initial={{ opacity: 0 }}
                    animate={{ opacity: 1 }}
                  >
                    <motion.div
                      className="w-16 h-16 border-4 border-primary-200 border-t-primary-500 rounded-full mx-auto mb-6"
                      animate={{ rotate: 360 }}
                      transition={{ duration: 1, repeat: Infinity, ease: "linear" }}
                    />
                    <h4 className="text-xl font-semibold text-gray-900 mb-4">
                       ...
                    </h4>
                    <div className="space-y-2 text-left max-w-md mx-auto">
                      <motion.div 
                        className="flex items-center space-x-2"
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        transition={{ delay: 0.5 }}
                      >
                        <CheckCircle className="w-5 h-5 text-green-500" />
                        <span className="text-gray-600">  (20,160 )</span>
                      </motion.div>
                      <motion.div 
                        className="flex items-center space-x-2"
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        transition={{ delay: 1 }}
                      >
                        <CheckCircle className="w-5 h-5 text-green-500" />
                        <span className="text-gray-600"> k-NN </span>
                      </motion.div>
                      <motion.div 
                        className="flex items-center space-x-2"
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        transition={{ delay: 1.5 }}
                      >
                        <CheckCircle className="w-5 h-5 text-green-500" />
                        <span className="text-gray-600">GNN </span>
                      </motion.div>
                      <motion.div 
                        className="flex items-center space-x-2"
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        transition={{ delay: 2 }}
                      >
                        <div className="w-5 h-5 border-2 border-primary-500 rounded-full animate-spin" />
                        <span className="text-gray-600"> ...</span>
                      </motion.div>
                    </div>
                  </motion.div>
                )}

                {demoStatus === 'completed' && (
                  <motion.div
                    className="text-center"
                    initial={{ opacity: 0, scale: 0.8 }}
                    animate={{ opacity: 1, scale: 1 }}
                  >
                    <motion.div
                      className="w-16 h-16 bg-green-100 rounded-full flex items-center justify-center mx-auto mb-6"
                      initial={{ scale: 0 }}
                      animate={{ scale: 1 }}
                      transition={{ type: "spring", stiffness: 200 }}
                    >
                      <CheckCircle className="w-10 h-10 text-green-500" />
                    </motion.div>
                    
                    <h4 className="text-2xl font-bold text-gray-900 mb-4">
                        !
                    </h4>
                    
                    <div className="bg-gray-50 rounded-xl p-6 mb-6">
                      <div className="grid grid-cols-2 gap-4 text-sm">
                        <div>
                          <div className="font-semibold text-gray-900"> :</div>
                          <div className="text-green-600">7.5 </div>
                        </div>
                        <div>
                          <div className="font-semibold text-gray-900">:</div>
                          <div className="text-green-600">15.2 GB</div>
                        </div>
                        <div>
                          <div className="font-semibold text-gray-900">:</div>
                          <div className="text-green-600">20,160 / 20,160</div>
                        </div>
                        <div>
                          <div className="font-semibold text-gray-900">:</div>
                          <div className="text-green-600">98.7%</div>
                        </div>
                      </div>
                    </div>
                    
                    <p className="text-gray-600 mb-6">
                        ,    200 .
                         !
                    </p>
                    
                    <button
                      onClick={downloadReport}
                      className="bg-primary-500 text-white px-6 py-3 rounded-lg font-semibold hover:bg-primary-600 transition-colors flex items-center space-x-2 mx-auto"
                    >
                      <Download className="w-5 h-5" />
                      <span>  </span>
                    </button>
                  </motion.div>
                )}
              </div>
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>
    </section>
  )
}

export default Demo

